* {
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #f3f4f4;
  color: #222;
}
.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

@media screen and (min-width: 920px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
    /* align-items: start;    1-ci par razmer*/
  }
}
.book {
  background: white;
  border-radius: 1rem;
  padding: 1rem 2rem;
}
.book h1 {
  margin-top: 0, 5rem;
}
.book h4 {
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}
.book p {
  margin-top: 0.5rem;
}
